/* PROFILE SECTION */
.profile-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-head,
.profile-description h3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.name-icon {
  display: flex;
  gap: 15px;
}

.logo-presentation {
  max-width: 190px;
}

.profile-description {
  margin: 30px 0;
}

.profile-description h3 {
  margin: 15px 0 40px 0;
}

/* SKILLS SECTION */
.skills-cards {
  display: flex;
  margin: 50px 0 15px 0;
  justify-content: space-around;
}

.card-head {
  display: flex;
  margin: 15px 0;
  border-radius: 5px;
  border: 1px solid #d1d1e9;
  justify-content: center;
}

.skills-cards .card-container {
  background-color: #1c1c1c; /* Fondo gris oscuro */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skills-cards .card-container:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.5);
}

.card-head h5 {
  margin: 0;
  padding: 10px 3px;
}

.card-row {
  display: grid;
  grid-template-columns: 1fr 4fr;
  align-items: center;
  padding: 5px 0;
}

.card-row p {
  margin-bottom: 0;
}

.card-row i {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-container {
  width: 18%;
  border-radius: 10px;
  padding: 10px 15px;
}

.skills-section {
  margin: 40px 0;
  padding: 30px 0;
  border-radius: 10px;
  color: #d1d1e9;
  background-color: #2e2e38;
}

/* CARUSEL SECTION */

.wrapper {
  display: flex;
  width: 600px;
  height: 330px;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
}

.card {
  flex: 1;
  transition: flex 0.5s ease;
  cursor: pointer;
}

.card p {
  width: 100%;
  margin: 10px 0;
  text-align: center;
  font-style: italic;
}

.card:hover {
  flex: 10;
}

.card-row:hover {
  color: #ffc107;
}

.card1,
.card2,
.card3,
.card4 {
  background-position: center;
  background-size: cover;
  transition: background-image 0.5s ease-in-out;
}

.carousel-frontend-pj {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px 0;
}

.card1:hover {
  background-image: url("../../assets/img/Prueba2.jpg") !important;
}

.carousel-container {
  padding: 15px 0;
}

/*******************************************************/
/* ------MEDIAQUEARIES ------*/
/*******************************************************/

/*******************************************************/
/* ------MOBILE ------*/
/*******************************************************/

@media (width <= 767px) {
  .name-icon {
    flex-direction: column;
    align-items: center;
  }

  .profile-head {
    text-align: center;
  }

  .profile-description p {
    text-align: justify;
  }

  .skills-section {
    background-color: #1c1c1c;
  }

  .skills-section .featured-2 {
    text-align: center;
  }

  .skills-cards {
    flex-direction: column;
    gap: 15px;
  }

  .card-container {
    width: 100%;
    background: linear-gradient(135deg, #2e2e38 0%, #3d3d4a 50%, #252534 100%);
  }
}

/*******************************************************/
/* ------TABLET ------*/
/*******************************************************/

@media (width >= 768px) and (width <= 991px) {
  .card-row {
    gap: 15px;
  }

  .card-container {
    width: 23%;
    padding: 10px;
  }
}
/*******************************************************/
/* ------LAPTOP ------*/
/*******************************************************/
@media (width >= 992px) and (width <= 1199px) {
  .card-container {
    width: 23%;
    padding: 10px;
  }

  .card-row {
    gap: 10px;
  }
}
